@import 'mixins';
@import 'variables';

@font-face {
	font-family: 'Brown';
	src: url('Brown-Light.eot');
	src: url('Brown-Light.eot?#iefix') format('embedded-opentype'),
		url('Brown-Light.woff') format('woff'),
		url('Brown-Light.ttf') format('truetype');
	font-weight: 300;
	font-style: normal;
}

@font-face {
	font-family: 'Brown';
	src: url('Brown-Thin.eot');
	src: url('Brown-Thin.eot?#iefix') format('embedded-opentype'),
		url('Brown-Thin.woff') format('woff'),
		url('Brown-Thin.ttf') format('truetype');
	font-weight: 100;
	font-style: normal;
}

@font-face {
	font-family: 'Brown';
	src: url('Brown-Regular.eot');
	src: url('Brown-Regular.eot?#iefix') format('embedded-opentype'),
		url('Brown-Regular.woff') format('woff'),
		url('Brown-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Brown';
	src: url('Brown-Bold.eot');
	src: url('Brown-Bold.eot?#iefix') format('embedded-opentype'),
		url('Brown-Bold.woff') format('woff'),
		url('Brown-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}

body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    font-family: 'Brown', Helvetica, Arial, sans-serif;
    font-style: normal;
    margin: 0;
    letter-spacing: 0.5px;
    padding: 0;
}

h1{ font-size: 48px; } //48px
h2{ font-size: 32px; } //32px
h3{ font-size: 24px; } //24px
h4{ font-size: 18px; } //18px
h5{ font-size: 16px; } //16px
h6{ font-size: 14px; } //13px
p{  font-size: 13px; }

@include phone{
  h1{ font-size: 44px; } //48px
  h2{ font-size: 28px; } //32px
  h3{ font-size: 20px; } //24px
  h4{ font-size: 16px; } //18px
  h5{ font-size: 15px; } //16px
  h6{ font-size: 13px; } //13px
  p{  font-size: 12px; }
}

body{
  background: white;

  .main-container{
    right: 0px;
    position: absolute;
    width: calc(100%);

    .inner-container{
      left: 310px;
      position: absolute;
      top: 0;
      width: calc(100% - 310px);
      @include single{
        left: 0;
        width: 100%;
      }

      .hero-block{
        background: none;
        display: block;
        height: 100vh;
        position: relative;
        text-align: center;

        .hero-inner{
          display: block;
          max-width: 650px;
          left: 50%;
          position: absolute;
          top: calc(50% - 30px);
          width: 90%;
          @include transform(translate(-50%,-50%));

          .hero-image-holder{
            display: block;
            left: -2px;
            margin: 0 auto 20px auto;
            max-width: 300px;
            width: 90%;
            overflow: hidden;

            @include phone{
              max-width: 220px;
              margin-bottom: 10px;
            }

            .hero-image{
              margin-left: -3.3%;
              width: 100%;
            }
          }

          .hero-headline{
            color: #666;
            font-weight: 500;
            margin-bottom: 10px;
            &:after{
              content: 'user experiences.';
              display: inline-block;
              padding-left: 8px;
              position: relative;

              @include single{
                padding-left: 6px;
              }
              @include phone{
                content: 'UX.';
                padding-left: 5px;
              }
            }
          }

          .hero-description{
            color: #ccc;
            line-height: 1.75rem;
            font-weight: 400;

            &:before{
              content: 'Currently b';
              display: inline-block;
              position: relative;

              @include phone{
                content: 'B';
                padding-left: 5px;
              }
            }

            &:after{
              content: 'Washington, DC.';
              display: inline-block;
              position: relative;
              padding-left: 7px;

              @include phone{
                content: 'DC.';
                padding-left: 5px;
              }

              @include phone{
                content: 'DC.';
                padding-left: 4px;
              }
            }

            a{
              color: #ccc;
              text-decoration: none;

              &:hover{
                text-decoration: underline;
              }
            }
          }
        }

        .down-arrow{
          bottom: 30px;
          cursor: pointer;
          display: block;
          height: 36px;
          left: 50%;
          position: absolute;
          width: 60px;
          @include transform( translateX(-50%));
          @include single{
            bottom: 60px;
          }

          &:before{
            content: '';
            display: block;
            background: #e6e6e6;
            height: 4px;
            left: 13.5px;
            position: absolute;
            top: 16px;
            width: 20px;
            @include transform( rotate(40deg));
          }
          &:after{
            content: '';
            display: block;
            background: #e6e6e6;
            height: 4px;
            right: 13.5px;
            position: absolute;
            top: 16px;
            width: 20px;
            @include transform( rotate(-40deg));
          }
        }
      }

      .project-block{
        background: white;
        display: block;
        padding: 120px 0;

        &#stash{
          background: #F7F9FA;
        }

        &#humble{
          background: #F7F9FA;
        }

        .project-intro{
          color: #666;
          text-align: center;

          h6{
            color: #dedede;
            letter-spacing: 1px;
            font-weight: 300;
            margin-bottom: 30px
          }

          h3{
            letter-spacing: 1px;
            line-height: 38px;
          }

          h5{
            letter-spacing: 1px;
            line-height: 38px;
            font-weight: 400;
          }

          .line-break{
            display: block;
            background: #666;
            height: 1px;
            margin: 22px auto 30px auto;
            opacity: 0.5;
            width: 60px;
          }
        }

        .project-description{
          color: #666;
          font-weight: 400;
          line-height: 1.375rem;
          margin: 0 auto 30px auto;
          width: 90%;
          max-width: 650px;
        }

        .project-image{
          -webkit-box-shadow: 0px 4px 18px 0px rgba(33,33,33,0.45);
          -moz-box-shadow: 0px 4px 18px 0px rgba(33,33,33,0.45);
          box-shadow: 0px 4px 18px 0px rgba(33,33,33,0.45);
          display: block;
          height: auto;
          margin: 50px auto;
          max-width: 750px;
          width: 90%;
        }

        .project-button{
          cursor: pointer;
          display: inline-block;
          left: 50%;
          position: relative;
          @include transform( translateX(-50%));

          button{
            background: transparent;
            border: 2px solid #666;
            color: #666;
            cursor: pointer;
            font-size: 1rem;
            padding: 15px 30px;
            @include transition( ease-in-out 0.1s );

            &#v_button{
              &:hover{
                border: 2px solid #2A2B2A;
                background: #2A2B2A;
                color: #F2C51F;
              }
            }
            &#s_button{
              &:hover{
                border: 2px solid #7A3DFE;
                background: #7A3DFE;
                color: white;
              }
            }
            &#r_button{
              &:hover{
                border: 2px solid black;
                background: black;
                color: white;
              }
            }
            &#h_button{
              &:hover{
                border: 2px solid #DB3B2A;
                background: #DB3B2A;
                color: white;
              }
            }
            &#as_button{
              &:hover{
                border: 2px solid #666;
                background: #666;
                color: white;
              }
            }
          }
        }
      }

      .skills-block{
        background: #8693AB;
        color: white;
        padding: 120px 0 70px 0;
        text-align: center;

        h3{
          letter-spacing: 1px;
          line-height: 38px;
        }

        .skills-list{

          .skill{

            margin: 50px 0;

            p{
              margin-top: 8px;
            }

            a{
              color: white;
              display: inline-block;
              position: relative;
              text-decoration: none;
              @include transition( ease-in-out 0.1s );

              &:hover{
                color: #2E4052;
                &:after{
                  border-bottom: 1px solid #2E4052;
                }
              }

              &:after{
                background: none;
                border-bottom: 1px solid white;
                content: "";
                display: block;
                height: 4px;
                left: 0;
                width: 100%;
                position: absolute;
                @include transition( ease-in-out 0.1s );
              }
            }
          }
        }
      }

      .contact-block{
        height: calc(100vh - 180px);
        padding: 120px 0 60px;
        position: relative;
        text-align: center;

        @include single{
          height:calc(100vh - 241px);
        }

        h3{
          color: #666;
        }

        .social-box{
          left: 50%;
          position: absolute;
          top: 50%;
          @include transform( translate(-50%, -50%));

          .email-button{
            margin: 0px 0 50px 0;
            cursor: pointer;
            display: inline-block;
            position: relative;

            button{
              background: transparent;
              border: 2px solid #666;
              color: #666;
              cursor: pointer;
              font-size: 1rem;
              padding: 15px 30px;
              @include transition( ease-in-out 0.1s );

              &:hover{
                background: #2E4052;
                border: 2px solid #2E4052;
                color: white;
              }
            }
          }

          .social-icons{
            li{
              display: inline-block;

              a{
                color: #ccc;
                font-size: 1.5rem;
                padding: 8px;
                margin: 0 8px;
                @include transition( ease-in-out 0.1s );

                &:hover{
                  color: #2E4052;
                }

                i{
                  .fa{
                    &.fa-twitter{
                      &:before{
                        content: '\f099';
                        display: inline-block;
                        position: relative;
                      }
                    }
                    &.fa-linkedin{
                      &:before{
                        content: '\f0e1';
                        display: inline-block;
                        position: relative;
                      }
                    }
                    &.fa-github{
                      &:before{
                        content: '\f09b';
                        display: inline-block;
                        position: relative;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .copyright{
          bottom: 60px;
          color: #dedede;
          left: 50%;
          position: absolute;
          @include transform( translateX(-50%));
        }
      }
    }
  }
}
